<template>
<div class="mobile-page">
    <MainLayout :title="$t('Common.Notice.DetailTitle')" :leftIcon="`LEFT_ARROW`" :leftAction="() => this.$router.go(-1)">
        <div class="padding-content">
            <div class="detail-header">
                <div class="title">{{notice.artclSbj ? notice.artclSbj : ''}}</div>
                <div class="time">{{noticeTime}}</div>
            </div>
            <div class="detail-content">{{notice.article ? notice.article : ''}}</div>
        </div>
    </MainLayout>
</div>
</template>

<script>
import MainLayout from '@/components/Common/Layout/MainLayout'
import moment from 'moment'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation'
import {
    FETCH_NOTICE_DETAIL
} from '@/mutations/Notice.mutation'
import {
    CODE_BBS
} from '@/constants'

export default {
    name: 'NotificationDetail',
    components: {
        MainLayout
    },
    data: function () {
        return {
            noticeTime: null
        }
    },
    props: {
        id: null
    },
    computed: {
        notice() {
            return this.$store.state.Notice.noticeDetail;
        },
    },
    async created() {
        await this.getNoticeDetail();

    },
    methods: {
        async getNoticeDetail() {
            try {
                let params = {
                    bbsCd: CODE_BBS.NOTICE,
                    artclSeq: this.id
                }
                await this.$store.dispatch("FETCH_NOTICE_DETAIL", params);
                if (this.notice && this.notice.regDt) {
                    this.noticeTime = moment(this.notice.regDt).format("YYYY.MM.DD HH:mm");
                }
            } catch (err) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, err.message);
                this.isError = true;
            }
        }
    }
}
</script>
