<template>
    <div class="container-fluid main-nav">
        <div class="row h-100 position-relative justify-content-between align-content-center">
            <div class="d-flex align-items-center nav-el" @click="leftAction">
                <span v-if="isLeftArrow" class="main-nav-left arrow"></span>
                <span v-if="isBar" class="main-nav-left bar"></span>
            </div>
            <div class="d-flex justify-content-center align-items-center nav-el cy-force-center">{{title}}</div>
            <div class="d-flex align-items-center nav-el">
                <span v-if="isHomeBar" class="main-nav-left bell margin-left" @click="gotoNotifications"></span>
                <span v-if="isHomeBar" class="main-nav-left profile" @click="gotoPersonalInfo"></span>
            </div>
        </div>
    </div>
</template>
<script>
import {NAV_ICONS} from '@/constants';
export default {
    name: 'MainNav',
    props: {
        title: String,
        leftAction: {
            type: Function,
            default: () => this.$router.go(-1),
        },
        leftIcon: {
            type: String,
            default: NAV_ICONS.BAR
        },
        isHomeBar: {
            type: Boolean,
            default: false,
        }
    },
    computed: {
        isLeftArrow: function () {
            return this.leftIcon === NAV_ICONS.LEFT_ARROW;
        },
        isBar: function () {
            return this.leftIcon === NAV_ICONS.BAR;
        }
    },
    methods: {
        gotoPersonalInfo() {
            this.$router.push('/personal-information')
        },

        gotoNotifications() {
            this.$router.push('/notifications')
        }
    }
}
</script>
<style lang="scss">
    @import '@/assets/styles/variables.scss';
    @import '@/assets/styles/main-nav.scss';
</style>